@media screen and (min-width:320px){
    .Orden_Cocina {
      padding: 10px;
      max-width: 318px;
      width: 50%;
    }
    .Titulo_orden{
      font-size: 150%;
    }
    .Tabla{
      font-size: 70%;
    }
    .next_orden{
      width: 35%;
      height: 35%;
      padding: 10px;
    }
   
  }
  
  @media screen and (min-width:480px){
    .Orden_Cocina {
      padding: 1em;
      height: 100%;
      width: 100%;
    }
  }
  
  @media screen and (min-width:768px){
    .Orden_Cocina {
      padding: 2em;
      height: 100%;
      width: 100%;
    }
  }
  
  @media screen and (min-width:1024px){
    .Orden_Cocina {
      padding: 2em;
      height: 100%;
      width: 100%;
    }
    .next_orden{
      width: 50px;
      height: 50px;
      padding: 5px;
      margin-top: 10%;
      
    }
    .Titulo_orden{
      font-size: 200%;
    }
    .Tabla{
      font-size: 120%;
    }
  }
.Orden_Cocina{
        width: 100%;
        max-width: 600px;
        padding: 45px;
        margin: auto;
        margin-top: 4%;
        height: 100%;
        text-align: center;
        background: #ffffff;
        -webkit-box-shadow: 0px 6px 79px 7px rgba(0,0,0,0.75);
       -moz-box-shadow: 0px 6px 79px 7px rgba(0,0,0,0.75);
       box-shadow: 0px 6px 79px 7px rgba(0,0,0,0.75);
        border-radius: 8px 8px 8px 8px;
        overflow: hidden;
        justify-content: center;
}
.next_orden{
  width: 20%;
  height: 20%;
  max-width: 25%;
  max-height: 25%;
  padding: 10px;
  margin: auto;
  display:flex; 
  margin-top: 10%;

}

.tablaCocina{
    margin: auto;
    padding: 20px;
   
}

.Tabla{
    
    width: 80%;
    height: 40%;
    justify-content:flex-start;  
    flex-direction: column;
    text-align: center;
    max-width: 500px;
    padding: 10px;
    margin: auto;
    height: 150%;
    justify-content: space-between;
    border-bottom: 3px solid #848280;
    
}
